/* Js-grid */

.jsgrid {
	.jsgrid-button {
		background-image: url(../../../assets/images/sprites/jsgrid-icons.png);
	}
	.jsgrid-grid-header {
		border: 1px solid $border-color;
		border-top: 0;
		background: $card-bg;
	}
	.jsgrid-table {
		@extend .table;
		th {
			font-weight: initial;
			font-family: $type1-bold;
		}
		.jsgrid-filter-row {
			input[type=text],
			select {
				@extend .form-control;
				max-height: 46px;
				min-width: 100px;
				&:focus {
					background: transparent;
				}
			}
			input[type=number] {
				@extend .form-control;
				padding: .56rem 0;
				max-height: 46px;
				&:focus {
					background: transparent;
					border: 1px solid $border-color;
				}
			}
			.jsgrid-cell {
				background: $card-bg;
			}
		}
	}
	.jsgrid-header-sort {
		&:before {
			margin-top: 10px;
			float: right;
		}
	}
	.jsgrid-header-sort-asc {
		&:before {
			border-color: transparent transparent $body-color;
		}
	}
	.jsgrid-header-sort-desc {
		&:before {
			border-color: $body-color transparent transparent
		}
	}
	.jsgrid-pager {
		line-height: 2;
		@extend .pagination;
		.jsgrid-pager-nav-button,
		.jsgrid-pager-page {
			@extend .page-item;
			a {
				@extend .page-link;
			}
		}
	}
	.jsgrid-pager-current-page {
		padding: 0.5rem 0.75rem;
		font-weight: initial;
		line-height: 1.25;
	}
	.jsgrid-header-row {
		.jsgrid-header-cell {
			background: inherit;
		}
	}
	.jsgrid-row {
		.jsgrid-cell {
			background: $card-bg;
		}
	}
	.jsgrid-alt-row {
		.jsgrid-cell {
			background: $table-accent-bg;
		}
	}
	.jsgrid-cell {
    border: 1px solid transparent; 
	}
	.jsgrid-grid-body {
		border: 1px solid $border-color;
	}
	.jsgrid-header-row {
		.jsgrid-header-cell {
			border: 1px solid $border-color;
		}
	}

	.jsgrid-insert-row {
		.jsgrid-cell {
			border: 1px solid $border-color;
			background: transparent;
			input {
				background: transparent;
				border: 1px solid $border-color;
				color: $body-color;
				padding: 10px;
			}
			select {
				background: transparent;
				border: 1px solid $border-color;
				color: $body-color;
				max-height: 46px;
			}
		}
	}
	.jsgrid-edit-row>.jsgrid-cell {
    background: theme-color(light);
	}
}